import React, { useContext } from 'react'
import { connect } from 'react-redux'
import { userLoginFetch } from '../../../store/action/Auth'
import { useForm } from '../../../components/useForm'
import { authenticate, isAuth } from '../../../helpers/auth'
import { DataContext } from '../../../LocalDB'
import { userPostFetch } from '../../../store/action/Auth'
import axios from 'axios';
import UI from './ui'

const Login = (props) => {
  const { addItem } = useContext(DataContext)
  const { _id } = props.Shop

  const initialFValues = {
    username: '',
    password: '',
  }
  const validate = (fieldValues = values) => {
    let temp = { ...errors }
    if ('username' in fieldValues)
      temp.username = fieldValues.username ? "" : "This field is required."
    if ('password' in fieldValues)
      temp.password = fieldValues.password ? "" : "This field is required."
    if ('password' in fieldValues)
      temp.password = fieldValues.password.length > 7 ? "" : "Minimum Length 8."
    setErrors({
      ...temp
    })

    if (fieldValues === values)
      return Object.values(temp).every(x => x === "")
  }
  const {
    values,
    errors,
    setErrors,
    setValues,
    validateOnChange
  } = useForm(initialFValues, true, validate);

  const handleInputChange = e => {
    const { name, value } = e.target
    setValues({
      ...values,
      [name]: value
    })
    if (validateOnChange)
      validate({ [name]: value })
  }
  const url = 'http://localhost:5000/api'
  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      if (isAuth()) {
        props.userLoginFetch(values)
      } else {
        axios
          .post(`${url}/login`, {
            email: values.username,
            password: values.password
          })
        .then(res => {
          authenticate(res, () => {
            isAuth() && isAuth().role === 'admin'
              ? props.history.push('/admin')
              : props.history.push('/private');
            console.log(values.username, values.password)
            console.log(res.data.user);
            if (_id) {
              props.userLoginFetch(values)
            } else {
              addItem('Shop', {
                Name: res.data.user.name,
                Type: 'Resturant',
                Contact: res.data.user.contact,
                Location: res.data.user.location,
                Pin: '1234',
                Bill: null,
                OT: null,
                Preferences: null
              }).then((result) => {
                props.userPostFetch({
                  username: values.username,
                  password: values.password,
                  Mobile: res.data.user.contact,
                  City: res.data.user.location,
                  shopid: result._id
                })
              })
            }
          });
        })
        .catch(err => {
          console.log(err.response);
        });
      }
    }
  }

  return (
    <UI
      values={values}
      errors={errors}
      handleInputChange={handleInputChange}
      handleSubmit={handleSubmit}
      isAuth={isAuth}
    />
  )
}

const mapStateToProps = (state) => {
  return {
    Auth: state.Auth,
    Shop: state.Shop,
  }
}

const mapDispatchToProps = dispatch => ({
  userLoginFetch: userInfo => dispatch(userLoginFetch(userInfo)),
  userPostFetch: userInfo => dispatch(userPostFetch(userInfo))
})

export default connect(mapStateToProps, mapDispatchToProps)(Login);
